<template>
  <div  :class="{isOpacity: isOpacity}" class="nav">
    <div class="width w">
      <div class="list">
        <ul>
          <li ><router-link to="/index" class="hvr-underline-from-center">主页</router-link></li>
          <li><router-link to="/list" class="hvr-underline-from-center">歌单</router-link></li>
          <li><router-link to="/support" class="hvr-underline-from-center">自选</router-link></li>
          <li><router-link to="/about" class="hvr-underline-from-center">我们</router-link></li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isOpacity: true
    }
  },
  mounted () {
    // 监听scroll事件 如果滑动则执行回调，将nav变为半透明
    window.addEventListener('scroll', this.navChange)
  },
  methods: {
    navChange () {
      let scrollTop = document.documentElement.scrollTop ||document.body.scrollTop
      this.isOpacity = scrollTop > 0 ? false : true
    }
  }
}
</script>

<style lang="scss">
  .isOpacity {
    background-color: transparent!important;
    transition: all 1s ease;
  }
  .nav {
    width: 10rem;
    position: fixed;
    height: 1.5rem;
    background-color: rgba(255, 255, 255, .8);
    z-index: 100;
    transition: all 1s ease;
    padding-top: 0.2rem;
    .width {
      .list {
        line-height: 1rem;
        ul {
          width: 10rem;
          display: flex;
          li {
            font-weight: bold;
            font-size: 0.6rem;
            text-align: center;
            width: 2.5rem;
            padding: 0;
            color: #000;
            a {
              color: rgba(53,53,53,0.5);

            }
          }
        }
      }
    }
  }
  .hvr-underline-from-center::before {
    background: #000;
  }
</style>



