<template>
  <swiper :options="swiperOption" class="swiper" ref="mySwiper">
    <swiper-slide>
      <div class="info">
        <router-link to="/wordpress"><img src="../assets/img/profile1.jpg" class="ani" swiper-animate-effect="fadeInDown" swiper-animate-duration="1s"></router-link>
        <p class="ani" swiper-animate-effect="fadeInDown" swiper-animate-duration="1s">Relient</p>
        <a href="https://codepen.io/RelientS/full/oMeRjR/" class="ani" swiper-animate-effect="fadeInDown" swiper-animate-duration="1s">Front-end Web Developer</a>
      </div>
    </swiper-slide>
    <swiper-slide>
         <div class="info">
        <a href="#"><img src="../assets/img/profile2.png" class="ani" swiper-animate-effect="rubberBand" swiper-animate-duration="1s"></a>
        <p class="ani" swiper-animate-effect="rubberBand" swiper-animate-duration="1s">山海</p>
        <a href="#" class="ani" swiper-animate-effect="rubberBand" swiper-animate-duration="1s">The owner of this server</a>
      </div>
    </swiper-slide>
  </swiper>
</template>

<script>
import * as swiperAni from '@/assets/js/swiper.animate.min.js'
export default {
      data(){
        return{
          swiperOption: {
              initialSlide: 0,
              direction: 'vertical',
              slidesPerView: 1,
              mousewheel: true,
              height: window.innerHeight,  // 高度设置，占满设备高度
              on: {
                init: function () {
                  swiperAni.swiperAnimateCache(this)
                  swiperAni.swiperAnimate(this)
                },
                slideChange: function () {
                  swiperAni.swiperAnimate(this)
                }
              }
          }
        }
      },
      computed: {
        swiper() {
          return this.$refs.mySwiper.swiper
        }
      }
};
</script>

<style lang="scss" scoped>
.swiper{
  max-height: 17rem;
}
.info{
  img{
    width: 5rem;
    height: 5rem;
    margin-top: 3.5rem;
  }
  p{
    font-size: 0.8rem;
    font-style: bold;
    margin: 0.3rem 0 0.2rem 0;
  }
  a{
    font-size: 0.4rem;
    color: rgba(53,53,53,.8);
  }
}
</style>



