<template>
  <div class="container">
    <iframe src="http://relient.cn/wordpress" id="myiframe" frameborder="0" width="100%" :height="height"></iframe>
  </div>
</template>

<style lang="scss" scoped>
  .container{
    padding-top: 1.5rem;
    width: 10rem;
    overflow: scroll;
  }
</style>

<script>
export default {
  data() {
    return {
      height:window.innerHeight,
    }
  }
}
</script>



