<template>
  <swiper :options="swiperOption" class="swiper" ref="mySwiper">
    <swiper-slide  v-for="(item,index) in items" :key="index">
       <div class="border ani" swiper-animate-effect="fadeInDown" swiper-animate-duration="1s">
        <img :src="item.src"><img :src="item.src" class="bg">
      </div>
        <div class="lyric ani" swiper-animate-effect="fadeIn" swiper-animate-duration="2s">歌词正在施工中...</div>
        <SongListX class="test ani" :index="index" @spot="spot" swiper-animate-effect="fadeInUp" swiper-animate-duration="1s"></SongListX>
    </swiper-slide>
  </swiper>
</template>

<script>
import * as swiperAni from '@/assets/js/swiper.animate.min.js'
import SongListX from '@/components/SongListX'
export default {
      components:{
        SongListX,
      },
      data(){
        return{
          num:0,
          swiperOption: {
              initialSlide: 0,
              direction: 'vertical',
              slidesPerView: 1,
              mousewheel: true,
              height: window.innerHeight,  // 高度设置，占满设备高度
              on: { //使得切换时触发动画
                init: function () {
                  swiperAni.swiperAnimateCache(this)
                  swiperAni.swiperAnimate(this)
                },
                slideChange: function () {
                  swiperAni.swiperAnimate(this)
                }
              }
          },
          items:[
            {src:require("../assets/img/list1.jpg")},
            {src:require("../assets/img/list2.jpg")},
            {src:require("../assets/img/list3.jpg")},
          ],
        }
      },
      methods:{
        spot:function(data){
           this.$emit("spot",data)
        }
      },
      computed: {
        swiper() {
          return this.$refs.mySwiper.swiper
        }
      }
};
</script>

<style lang="scss" scoped>
.swiper{
  max-height: 18rem;
  .border{
    width: 7rem;
    height: 11.8rem;
    padding: 2rem 2.5rem 0.5rem 2.5rem;
     img{
       height: 5rem;
       width: 5rem;
       border-radius: 50%;
     }
     .bg{
       top:2.4rem;
       filter: blur(10px) brightness(80%) opacity(.8);
       position: fixed;
       z-index: -1;
     }
    }
.test{
  height: 1.5rem;
  position: absolute;
  bottom: 0;
}
  p{
    font-size: 0.8rem;
    font-style: bold;
    text-align: center;
  }
  a{
    font-size: 0.4rem;
    color: rgba(53,53,53,.8);
  }
  .lyric{
    font-weight: bold;
    font-size: 0.6rem;
    text-align: center;
  }
}
</style>



