<template>
  <swiper :options="swiperOption" class="swiper">
    <swiper-slide><a @click="music='http://132.232.74.205/music/gray.mp3';name='グレイ – sajou no hana';spot()"><img src="../assets/img/song0.jpg"></a></swiper-slide>
    <swiper-slide><a @click="music='http://other.web.nc01.sycdn.kuwo.cn/resource/n1/70/58/1869906380.mp3';name='On Your Own - Green River Ordinance';spot()"><img src="../assets/img/song1.jpg"></a></swiper-slide>
    <swiper-slide><a @click="music='http://other.web.rh01.sycdn.kuwo.cn/resource/n1/19/74/3151839674.mp3';name='Automobile - Kaleo';spot()"><img src="../assets/img/song2.jpg"></a></swiper-slide>
    <swiper-slide><a @click="music='http://other.web.rh01.sycdn.kuwo.cn/resource/n1/50/67/3892704192.mp3';name='Kaimu - ONE OK ROCK';spot()"><img src="../assets/img/song3.jpg"></a></swiper-slide>
    <swiper-slide><a @click="music='http://other.web.rh01.sycdn.kuwo.cn/resource/n3/33/67/362100825.mp3';name='Bohemian Rhapsody - Panic! At the disco';spot()"><img src="../assets/img/song4.jpg"></a></swiper-slide>
    <swiper-slide><a @click="music='http://other.web.rc01.sycdn.kuwo.cn/resource/n3/84/45/1661265743.mp3';name='Cancer - My Chemical Romance';spot()"><img src="../assets/img/song5.jpg"></a></swiper-slide>
    <swiper-slide><a @click="music='http://other.web.nf01.sycdn.kuwo.cn/resource/n3/4/46/146773812.mp3';name='Lemon - ハチ';spot()"><img src="../assets/img/song6.jpg"></a></swiper-slide>
    <swiper-slide><a @click="music='http://www.170hi.com/kw/other.web.nf01.sycdn.kuwo.cn/resource/n3/23/4/2572463444.mp3';name='Sunday Morning - Maroon5';spot()"><img src="../assets/img/song7.jpg"></a></swiper-slide>
    <swiper-slide><a @click="music='http://other.web.rd01.sycdn.kuwo.cn/resource/n1/33/89/1910830678.mp3';name='Man On The Moon - Phillip Phillips';spot()"><img src="../assets/img/song8.jpg"></a></swiper-slide>
    <swiper-slide><a @click="music='http://sg.sycdn.kuwo.cn/resource/n1/92/16/475399748.mp3';name='Up And Up (Acoustic) – Relient K';spot()"><img src="../assets/img/song9.jpg"></a></swiper-slide>
    <div class="swiper-pagination swiper-pagination-white" slot="pagination"></div>
  </swiper>
</template>

<script>
export default {
      data(){
        return{
          music:"",
          name:"",
          swiperOption: {
          autoplay: true,
          speed: 1000,
          loop: true,
          pagination: {
            el: '.swiper-pagination',
            clickable: true,
            }
          }
        }
      },
      methods:{
        spot:function(){
           this.$emit("spot",[this.music,this.name]);
      }
    }

};
</script>

<style lang="scss" scoped>
.swiper{
  max-width: 10rem;
  img{
      width: 10rem;
      height: 9rem;
            }
}
.swiper-pagination{
  outline: none;
}

</style>



