<template>
  <div class="songList">
   <swiper :options="swiperOption" class="swiper" ref="mySwiper">
     <swiper-slide  v-for="(item,num) in items[this.index]" :key="num" class="slide">
       <a><h1>{{ item.name }}<p><img src="../assets/img/run1.png" @click="name=item.name+' - '+item.singer;music=item.address;send()">{{ item.singer }}</p></h1></a>
     </swiper-slide>
 </swiper>
</div>
</template>

<script>
export default {
  props:{
    index:{
      type:Number,
      required:true
    }
  },
  data(){
    return{
      name:"",
      music:"",
      swiperOption: {
          initialSlide: 0,
          freeMode:false,
        },
      items:[
        [
          {"name":"Fake Your Death",singer:"My Chemical Romance",address:"http://other.web.re01.sycdn.kuwo.cn/resource/n3/29/92/3648828204.mp3"},
          {"name":"Cancer",singer:"My Chemical Romance",address:"http://other.web.rc01.sycdn.kuwo.cn/resource/n3/84/45/1661265743.mp3"},
          {"name":"The World is Ugly",singer:"My Chemical Romance",address:"http://other.web.rc01.sycdn.kuwo.cn/resource/n3/67/37/735770096.mp3"},
          {"name":"Welcome to the black parade",singer:"My Chemical Romance",address:"http://other.web.ra01.sycdn.kuwo.cn/resource/n1/320/48/10/1671343857.mp3"},
          {"name":"Boy Division",singer:"My Chemical Romance",address:"http://other.web.rc01.sycdn.kuwo.cn/resource/n1/51/68/2386442190.mp3"},
          {"name":"I'm Not Okay (I Promise)",singer:"My Chemical Romance",address:"http://other.web.ra01.sycdn.kuwo.cn/resource/n2/modifymp3/83/3835820565.mp3"},
          {"name":"The Ghost Of You",singer:"My Chemical Romance",address:"http://other.web.ra01.sycdn.kuwo.cn/resource/n1/modifymp3/24/3251332451.mp3"},
          {"name":"Bulletproof Heart",singer:"My Chemical Romance",address:"http://other.web.ra01.sycdn.kuwo.cn/resource/n3/2010/11/18/489695845.mp3"},
          {"name":"The Sharpest Lives",singer:"My Chemical Romance",address:"http://other.web.rc01.sycdn.kuwo.cn/resource/n2/74/28/1155878497.mp3"}
        ],
        [
          {"name":"The Five of Us Are Dying (Rough Mix)",singer:"My Chemical Romance",address:"http://other.web.rh01.sycdn.kuwo.cn/resource/n1/96/0/554522586.mp3"},
          {"name":"Kill All Your Friends",singer:"My Chemical Romance",address:"http://sd.sycdn.kuwo.cn/resource/n3/57/30/541862665.mp3"},
          {"name":"Mama",singer:"My Chemical Romance",address:"http://other.web.rc01.sycdn.kuwo.cn/resource/n2/0/82/3265020245.mp3"},
          {"name":"Na Na Na (Na Na Na Na Na Na Na Na Na)",singer:"My Chemical Romance",address:"http://other.web.ri01.sycdn.kuwo.cn/resource/n1/97/99/4248972132.mp3"},
          {"name":"Fake Your Death",singer:"My Chemical Romance",address:"http://other.web.re01.sycdn.kuwo.cn/resource/n3/29/92/3648828204.mp3"},
          {"name":"Disenchanted",singer:"My Chemical Romance",address:"http://other.web.rc01.sycdn.kuwo.cn/resource/n2/55/58/730855662.mp3"},
          {"name":"Teenagers",singer:"My Chemical Romance",address:"http://other.web.ri01.sycdn.kuwo.cn/resource/n2/79/30/3952372532.mp3"},
          {"name":"Dead!",singer:"My Chemical Romance",address:"http://other.web.rl01.sycdn.kuwo.cn/resource/n1/4/64/3444227252.mp3"}
        ],
        [
          {"name":"Listen",singer:"ONE OK ROCK",address:"http://other.web.rn01.sycdn.kuwo.cn/resource/n2/4/84/3492916630.mp3"},
          {"name":"One Way Ticket",singer:"ONE OK ROCK",address:"http://other.web.ri01.sycdn.kuwo.cn/resource/n3/61/6/1382842904.mp3"},
          {"name":"The Beginning",singer:"ONE OK ROCK",address:"http://other.web.rh01.sycdn.kuwo.cn/resource/n1/41/6/819365699.mp3"},
          {"name":"Riot!!!",singer:"ONE OK ROCK",address:"http://other.web.rh01.sycdn.kuwo.cn/resource/n3/27/13/1629217775.mp3"},
          {"name":"完全感覚Dreamer",singer:"ONE OK ROCK",address:"http://other.web.rh01.sycdn.kuwo.cn/resource/n1/39/95/232564369.mp3"},
          {"name":"Suddenly",singer:"ONE OK ROCK",address:"http://other.web.rh01.sycdn.kuwo.cn/resource/n1/2/15/2840308894.mp3"},
          {"name":"Liar",singer:"ONE OK ROCK",address:"http://other.web.rh01.sycdn.kuwo.cn/resource/n2/38/94/4263763790.mp3"}
        ]
      ]
    }
  },
  methods:{
    send:function(){
       this.$emit("spot",[this.music,this.name]);
   }
  }
}
</script>

<style lang="scss" scoped>
.songList{
  width: 10rem;
  height: 10.7rem;
  .swiper{
    width: 10rem;
    height: 6.3rem;
    overflow: hidden;
    a{
     color:rgba(53,53,53,1);
    }
    h1{
     width: 10rem;
     height: 1.5rem;
     padding: 0.5rem 2rem 0.5rem 0.5rem;
     font-size: 0.5rem;
     text-align: left;
     line-height: 0.1rem;
     border: 1px solid rgba(53,53,53,.3);
     background: rgba(255,255,255,.8);
     text-overflow: ellipsis;
     overflow: hidden;
     white-space: nowrap;
     p{
       width: 5rem;
       margin: 0.1rem 0.05rem;
       font-size: 0.3rem;
       line-height: 0.8rem;
       color:rgba(155,155,155,.8);

     }
     img{
       position: fixed;
       margin:-0.45rem 0 0 8rem;
       height: 1rem;
       width: 1rem;
     }
    }
  }
}
</style>



