<template>
  <div class="main">
    <h1>推荐单曲</h1>
     <SwiperX @spot="spot"></SwiperX>
     <div class="list">
       <h1>推荐歌单</h1>
       <ul>
         <li v-for="(item,index) in items">
           <a v-bind:href="item.address">
             <img v-bind:src="item.src">
             <p>{{ item.info }}</p>
           </a>
          </li>
       </ul>
     </div>
    <div class="content">{{ currentdate }}</div>
  </div>
</template>

<script>
import SwiperX from '@/components/SwiperX'
export default {
  components: {
    SwiperX
  },
  data(){
    return{
      currentdate:"",
      items:[
        {"info":"MyChemicalRomance:再见，青春。","src":require("../assets/img/list1.jpg"),"address":"https://music.163.com/#/playlist?id=10651367"},
        {"info":"my chemical romance","src":require("../assets/img/list2.jpg"),"address":"https://music.163.com/#/playlist?id=2467999949"},
        {"info":"ONEOKROCK","src":require("../assets/img/list3.jpg"),"address":"https://music.163.com/#/playlist?id=2602319"},
        {"info":"♡ONEOKROCK♡迷妹迷弟必备歌单ミ","src":require("../assets/img/list4.jpg"),"address":"https://music.163.com/#/playlist?id=373652200"},
        {"info":"Panic! At the Disco 合辑","src":require("../assets/img/list5.jpg"),"address":"https://music.163.com/#/playlist?id=785284516"},
        {"info":"无尽漫威世界，永恒的斯坦李Stan Lee","src":require("../assets/img/list6.jpg"),"address":"https://music.163.com/#/playlist?id=2511465227"},
      ]
    }
  },
  mounted: function () {
    this.$nextTick(function () {
        setInterval(this.getTime, 1000);
    })
  },
  methods:{
    getTime:function() {
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var hour = date.getHours();
      var min = date.getMinutes();
      var sec = date.getSeconds();
      var strDate = date.getDate();
        if (month >= 1 && month <= 9) {
          month = "0" + month;
          }
        if (strDate >= 0 && strDate <= 9) {
          strDate = "0" + strDate;
          }
        if (hour >=0 && hour <= 9)
          {
            hour = "0"+ hour;
          }
        if (min >=0 && min <= 9)
            {
              min = "0"+ min;
            }
        if (sec >=0 && sec <= 9)
          {
            sec = "0"+ sec;
          }
      var time = year + "-" + month + "-" + strDate + " " + hour + ":" + min + ":" + sec;
            this.currentdate = time;
     },
     spot:function(data){
        this.$emit("spot",data)
     }
  }
}
</script>

<style lang="scss" scoped>
h1{
  margin:0.1rem 0;
  padding: 0.1rem 0.2rem;
  border-left: 0.1rem solid red;
  font-size: 0.4rem;
}
  .main{
    height: 23.5rem;
    padding-top: 1.5rem;
    max-width: 10rem;
    .content{
      width: 10rem;
      height: 3rem;
      text-align: center;
      padding-top: 1.3rem;
      font-size: 0.35rem;
         }
    .list{
      width: 10rem;
      height: 10rem;

      ul{
        padding: 0;
        display: flex;
        flex-wrap: wrap;
        li{
          width: 3.23rem;
          height: 4.23rem;
          margin: 0.1rem 0.05rem 0.1rem 0.05rem;
          p{
            word-wrap:break-word;
            color:rgba(53,53,53,0.9);
            font-style:Microsoft YaHei;
            height: 1rem;
            width: 3.23rem;
            padding: 0 0.2rem;
            line-height: 0.36rem;
            font-size: 0.31rem;
          }
        }
        img{
          width: 3.23rem;
          height: 3.23rem;
        }
      }
    }
  }
</style>



