<template>
  <div class="main1">
    <div class="mask"></div>
    <ListSwiper class="test" @spot="spot"></ListSwiper>
  </div>
</template>

<script>
import ListSwiper from '@/components/ListSwiper'
export default {
  components: {
    ListSwiper,
  },
  methods:{
    spot:function(data){
       this.$emit("spot",data)
    }
  }
}
</script>

<style lang="scss" scoped>
  .main1{
    width: 10rem;
    background: url('~@/./assets/img/bg9.jpg') fixed center no-repeat;
    background-size:300% 100%;
    position: relative;
    .test{
      max-width: 10rem;
      width: 10rem;
      z-index: 3;
    }
    .mask{
      width: 10rem;
      height: 18rem;
      background: rgba(255,255,255,.5);
      position: absolute;
      top:0;
      z-index: 2;
    }
  }
</style>



