import Vue from 'vue'
import Router from 'vue-router'
import Header from '@/components/Header'
import Index from '@/pages/index'
import List from '@/pages/list'
import Support from '@/pages/support'
import About from '@/pages/about'
import Main from '@/pages/main'
import WordPress from '@/pages/wordpress'

Vue.use(Router)

export default new Router({
  routes: [
    {
        path:'',
        redirect:"main",
        component:Main,
        children:[
          {
              path:"",
              redirect:"index",
              meta: {
                keepAlive: true
              }
          },
            {
                path:'index',
                component:Index,
                meta: {
                  keepAlive: true
                }
            },
            {
                path:'list',
                component:List,
                meta: {
                  keepAlive: true
                }
            },
            {
                path:'support',
                component:Support,
                meta: {
                  keepAlive: true
                }
            },
            {
                path:'about',
                component:About,
                meta: {
                  keepAlive: true
                }
            },
            {
                path:'wordpress',
                component:WordPress,
                meta: {
                  keepAlive: true
                }
            }
        ]
    }
  ],
  scrollBehavior(){
    return {x:0,y:0}
  }
})



