<template>
  <div class="support">
    <div class="list">
      <img class="header animated" src="http://p0.ifengimg.com/pmop/2018/0211/5423A31B700419FFA98CB132A47F6FCE6021E64E_size659_w400_h300.gif" ref="img1">
      <ul>
        <li v-for="(item,index) in items" class="animated scl" :id="item.name">
          <a @click="picker(index)">
            <img :src="item.src">
            <div class="play" v-show="item.show"><img src="../assets/img/play3.gif"></div>
            <p>{{ item.info }}</p>
          </a>
         </li>
      </ul>
    </div>
    <div class="picker_bg animated" id="picker_bg" :style="{display:picker_dsp}" :class="{fadeInLeft:isFaded,fadeOutLeft:!isFaded}" @click="back()">
     <div class="picker" id="picker">
      <div class="picker_header">{{ name }}</div>
        <button id="picker_play" class="first" @click="play()">试听</button>
        <button id="picker_add" @click="add()">加入歌单</button>
        <button id="picker_download" @click="dl()">下载到本地</button>
    </div>
   </div>
   <div class="picker_bg animated" :style="{display:custom_dsp}" id="custom_bg" :class="{bounceInLeft:isUp,bounceOutLeft:!isUp}" @click="down()">
     <div class="custom" id="custom" @click.stop>
      <p>播放列表<img src="../assets/img/play3.png" @click="listplay()"></p>
       <ul>
         <li v-if="flag" style="text-align:center;height:8rem;line-height:1.5rem;padding:0 0.9rem;">这里还空空如也噢<br>快点击图片自建歌单吧<br>选好歌单之后<br>点击右上角的播放键<br>开始享受音乐吧~</li>
         <li v-for="(item,index) of customList"><p>{{ item.info }}</p><img src="../assets/img/close1.png" @click="del(index)"></li>
       </ul>
     </div>
   </div>
   <div class="circle" @click="up()" id="circle"><img src="../assets/img/menu.png"></div>
  </div>
</template>

<script>
  import scrollReveal from 'scrollreveal';
  export default {
    data(){
      return{
        scrollReveal:scrollReveal(),
        flip:false,
        flag:true,
        isFaded:true,
        isUp:true,
        picker_dsp:"none",
        custom_dsp:"block",
        name:"",
        address:"",
        info:"",
        index:"",
        customList:[],
        items:[
         {"name":"li1","info":"グレイ – sajou no hana","src":require("../assets/img/song0.jpg"),"address":"http://132.232.74.205/music/gray.mp3","show":false},
         {"name":"li2","info":"On Your Own - Green River Ordinance","src":require("../assets/img/song1.jpg"),"address":"http://other.web.nc01.sycdn.kuwo.cn/resource/n1/70/58/1869906380.mp3","show":false},
         {"name":"li3","info":"Automobile - Kaleo","src":require("../assets/img/song2.jpg"),"address":"http://other.web.rh01.sycdn.kuwo.cn/resource/n1/19/74/3151839674.mp3","show":false},
         {"name":"li4","info":"Kaimu - ONE OK ROCK","src":require("../assets/img/song3.jpg"),"address":"http://other.web.rh01.sycdn.kuwo.cn/resource/n1/50/67/3892704192.mp3","show":false},
         {"name":"li5","info":"Bohemian Rhapsody - Panic! At the disco","src":require("../assets/img/song4.jpg"),"address":"http://other.web.rh01.sycdn.kuwo.cn/resource/n3/33/67/362100825.mp3","show":false},
         {"name":"li6","info":"Cancer - My Chemical Romance","src":require("../assets/img/song5.jpg"),"address":"http://other.web.rc01.sycdn.kuwo.cn/resource/n3/84/45/1661265743.mp3","show":false},
         {"name":"li7","info":"Lemon - ハチ","src":require("../assets/img/song6.jpg"),"address":"http://other.web.nf01.sycdn.kuwo.cn/resource/n3/4/46/146773812.mp3","show":false},
         {"name":"li8","info":"Sunday Morning - Maroon5","src":require("../assets/img/song7.jpg"),"address":"http://www.170hi.com/kw/other.web.nf01.sycdn.kuwo.cn/resource/n3/23/4/2572463444.mp3","show":false},
         {"name":"li9","info":"Man On The Moon - Phillip Phillips","src":require("../assets/img/song8.jpg"),"address":"http://other.web.rd01.sycdn.kuwo.cn/resource/n1/33/89/1910830678.mp3","show":false},
         {"name":"li10","info":"Up And Up (Acoustic) – Relient K","src":require("../assets/img/song9.jpg"),"address":"http://sg.sycdn.kuwo.cn/resource/n1/92/16/475399748.mp3","show":false},
         {"name":"li11","info":"煙突 – ミツメ","src":require("../assets/img/song10.jpg"),"address":"http://other.web.rb01.sycdn.kuwo.cn/resource/n2/25/82/4160536369.mp3","show":false},
         {"name":"li12","info":"Dasher – Gerard Way&Lydia Night","src":require("../assets/img/song11.jpg"),"address":"http://sj.sycdn.kuwo.cn/resource/n2/2/74/1127395414.mp3","show":false},
         {"name":"li13","info":"最好的我 – 房祖名&龚芝怡","src":require("../assets/img/song12.jpg"),"address":"http://other.web.ri01.sycdn.kuwo.cn/resource/n1/21/63/1853039623.mp3","show":false},
         {"name":"li14","info":"Way Back Into Love – Hugh Grant&Haley Bennett","src":require("../assets/img/song13.jpg"),"address":"http://other.web.nb01.sycdn.kuwo.cn/resource/n1/5/45/2941611415.mp3","show":false},
         {"name":"li15","info":"枫 – 周杰伦","src":require("../assets/img/song14.jpg"),"address":"http://other.web.ra01.sycdn.kuwo.cn/resource/n3/128/43/28/1310690697.mp3","show":false},
         {"name":"li16","info":"轨迹 – 周杰伦","src":require("../assets/img/song15.jpg"),"address":"http://other.web.ra01.sycdn.kuwo.cn/resource/n1/128/10/84/1621136438.mp3","show":false},
         {"name":"li17","info":"瞬き – back number","src":require("../assets/img/song16.jpg"),"address":"http://other.web.nf01.sycdn.kuwo.cn/resource/n2/57/93/791389573.mp3","show":false},
         {"name":"li18","info":"Fast Car – Boyce Avenue","src":require("../assets/img/song17.jpg"),"address":"http://other.web.nb01.sycdn.kuwo.cn/resource/n3/63/69/1008934295.mp3","show":false},
         {"name":"li19","info":"Bluebird – 伍々慧","src":require("../assets/img/song18.jpg"),"address":"http://other.web.rs01.sycdn.kuwo.cn/resource/n1/89/76/4229950985.mp3","show":false},
         {"name":"li20","info":"花 – 岸部眞明","src":require("../assets/img/song19.jpg"),"address":"http://other.web.ri01.sycdn.kuwo.cn/resource/n3/49/74/1382509077.mp3","show":false},
         {"name":"li21","info":"風の詩 – 押尾コータロー","src":require("../assets/img/song20.jpg"),"address":"http://other.web.rh01.sycdn.kuwo.cn/resource/n2/39/88/1982390446.mp3","show":false},
         {"name":"li22","info":"Wednesday – アナログフィッシュ","src":require("../assets/img/song21.jpg"),"address":"http://music.163.com/song/media/outer/url?id=29482840.mp3","show":false},
         {"name":"li23","info":"瓦解 – 南拳妈妈","src":require("../assets/img/song22.jpg"),"address":"http://other.web.ra01.sycdn.kuwo.cn/resource/n3/128/8/19/2433116290.mp3","show":false},
         {"name":"li24","info":"Running – James Bay","src":require("../assets/img/song23.jpg"),"address":"http://other.web.ri01.sycdn.kuwo.cn/resource/n1/19/93/1294215358.mp3","show":false},
         {"name":"li25","info":"Grey Room – Damien Rice","src":require("../assets/img/song24.jpg"),"address":"http://music.163.com/song/media/outer/url?id=17240022.mp3","show":false},
         {"name":"li26","info":"She's a Riot – The Jungle Giants","src":require("../assets/img/song25.jpg"),"address":"http://music.163.com/song/media/outer/url?id=27676869.mp3","show":false},
         {"name":"li27","info":"Holiday – The Kinks","src":require("../assets/img/song26.jpg"),"address":"http://music.163.com/song/media/outer/url?id=19455183.mp3","show":false},
         {"name":"li28","info":"What A Mystery – Josh Wilson","src":require("../assets/img/song27.jpg"),"address":"http://other.web.rc01.sycdn.kuwo.cn/resource/n1/88/35/627126291.mp3","show":false},
         {"name":"li29","info":"Boston – Augustana","src":require("../assets/img/song28.jpg"),"address":"http://other.web.ri01.sycdn.kuwo.cn/resource/n2/34/84/1427948269.mp3","show":false},
         {"name":"li30","info":"Always Be With You – Walking On Cars","src":require("../assets/img/song29.jpg"),"address":"http://other.web.rh01.sycdn.kuwo.cn/resource/n3/94/38/2843150250.mp3","show":false},
        ]
      }
  },
  mounted:function(){
    var audio = document.getElementById("audio");
    var total = document.documentElement.clientHeight;
    var picker_bg = document.getElementById("picker_bg");
    var custom_bg = document.getElementById("custom_bg");
    var picker = document.getElementById("picker");
    var custom = document.getElementById("custom");
    var picker_play = document.getElementById("picker_play");
    var picker_add = document.getElementById("picker_add");
    var picker_download = document.getElementById("picker_download");
    var circle = document.getElementById("circle");
    picker.style.top= total * 0.2 +"px";
    picker.style.height= total * 0.6 +"px";
    picker_play.style.height= total * 0.1 +"px";
    picker_add.style.height= total * 0.1 +"px";
    picker_download.style.height= total * 0.1 +"px";
    picker_bg.style.height= total +"px";
    custom_bg.style.height= total +"px";
    custom.style.top= total * 0.1 +"px";
    custom.style.height= total * 0.69 +"px";
    this.scrollReveal.reveal('.scl',{
      duration:2000,
      delay:300,
      origin:'top',
      reset:true,
      mobile:true,
      distance:'1.5rem'
    })
  },
  methods:{
    play:function(){
      this.isFaded = false;
      let _this = this;
      let item = this.items;
      for(var i=0;i<item.length;i++)
        {
          item[i].show = false;
        }
      this.items[this.index].show = true;
      setTimeout(function(){_this.picker_dsp='none'},1000);
      this.$emit("spot",[this.address,this.name]);
      audio.onended = function(){
        _this.items[_this.index].show = false;
      }
    },
    add:function(){
      let _this = this;
      _this.customList.push(_this.info);
      console.log(_this.customList);
      this.isFaded = false;
      this.flag = false;
      setTimeout(function(){_this.picker_dsp='none'},1000)
    },
    dl:function(){
      this.isFaded = false;
      let _this = this;
      setTimeout(function(){_this.picker_dsp='none'},1000);
      alert("手机暂不支持此功能");
    },
    picker:function(index){
      this.picker_dsp = "block";
      this.isFaded = true;
      this.index = index;
      this.info = this.items[index];
      this.name = this.items[index].info;
      this.address = this.items[index].address;
    },
    back:function(){
      this.isFaded = false;
      let _this = this;
      setTimeout(function(){_this.picker_dsp='none'},500)
    },
    up:function(){
      circle.style.display = "none";
      this.custom_dsp = "block";
      this.isUp = true;
    },
    down:function(){
      this.isUp = false;
      let _this = this;
      setTimeout(function(){_this.custom_dsp='none';circle.style.display = "block";},500);
    },
    listplay:function(){
      let _this = this;
      var list = this.customList;
      let name = this.customList[0].info;
      let song = this.customList[0].address;
      audio.preload = true;
      audio.loop = false;
      _this.$emit("spot",[song,name]);
      audio.onended = function(){
        list.shift();
        if(list[0]){
          song = list[0].address;
          name = list[0].info;
          _this.$emit("spot",[song,name]);
        }else{
          return false;
        }
      };
    },
    del:function(index){
       if(this.customList.length >= 2)
       {
         this.customList.splice(index,1);
       }
       else {
         this.customList = [];
         this.flag = true;
       }
    },
  }
}
</script>

<style lang="scss" scoped>
  .fadeOut{
    animation-duration: 0.5s;
  }
  .picker_bg{
    width: 10rem;
    position: fixed;
    top:0;
    z-index: 98;
  }
  .picker{
    background-color: rgba(233,233,233,0.9);
    color:rgba(255,54,93,1);
    border-radius:0.3rem;
    width: 8rem;
    margin:0 1rem;
    padding-top: 1rem;
    z-index:99;
    position: fixed;
    font-size: 0.7rem;
    text-align: center;
    font-weight: bold;
    .picker_header{
      margin-bottom: 0.5rem;
      font-size: 0.6rem;
      padding: 0 0.7rem;
      line-height: 0.8rem;
    }
    .first{
     border: none;
    }
    button{
      font-weight: bold;
      width: 8rem;
      height: 1rem;
      font-size: 0.7rem;
      line-height: 1rem;
      border-top: 1px solid rgba(53,53,53,0.5);
      color:rgba(255,54,93,1);
      background-color: rgba(233,233,233,1);
    }
  }

  .custom{
    background-color: rgba(233,233,233,0.9);
    color:rgba(255,54,93,1);
    border-radius:0.3rem;
    width: 8rem;
    margin:0 1rem 0 0.1rem;
    z-index:99;
    position: fixed;
    font-size: 0.9rem;
    text-align: center;
    overflow: scroll;
    p{
      width: 8rem;
      margin-bottom: 0.5rem;
    }
    img{
      width: 1.5rem;
      height: 1.5rem;
      position: relative;
      top:0.4rem;
      left: 0.7rem;
    }
    ul{
      width: 8rem;
      font-size: 0.8rem;
      border-top: 1px solid rgba(53,53,53,0.5);
      li{
        width: 8rem;
        height: 2rem;
        line-height: 2rem;
        background-color: rgba(233,233,233,1);
        border-bottom: 1px solid rgba(53,53,53,0.5);
        font-size: 0.6rem;
        text-align: left;
        display: flex;
        p{
          width: 5.7rem;
          height: 2rem;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          padding: 0 0.25rem 0 0.8rem;
        }
        img{
          width: 0.8rem;
          height: 0.8rem;
          margin: 0.6rem 0;
          position: relative;
          top:0;
        }
      }
    }
  }

  .support{
    width: 10rem;
    padding-top: 1.5rem;
    .list{
      width: 10rem;
      font-size: 0.35rem;
      .header{
        width: 10rem;
        margin-bottom: 0.3rem;
      }
      .play{
        width: 3.23rem;
        height: 3.23rem;
        position: absolute;
        top: 0;
        left: 0;
      }
      ul{
        padding: 0;
        display: flex;
        flex-wrap: wrap;
        li{
          width: 3.23rem;
          height: 4.23rem;
          margin: 0.1rem 0.05rem 0.1rem 0.05rem;
          text-overflow: ellipsis;
          position: relative;
          p{
            color:rgba(53,53,53,0.9);
            font-style:Microsoft YaHei;
            height: 1rem;
            width: 3.23rem;
            padding: 0 0.2rem;
            line-height: 0.36rem;
            font-size: 0.31rem;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
          }
        }
        img{
          width: 3.23rem;
          height: 3.23rem;
        }
      }
    }
  }

  .circle{
    display: none;
    background-color: rgba(233,233,233,0.9);
    width: 1.2rem;
    height: 1rem;
    position: fixed;
    top:2rem;
    left: 0;
    text-align: center;
    z-index: 97;
    border-radius:0  0.2rem 0.2rem 0;
    img{
      margin-top:0.15rem;
      width: 1rem;
      height: 0.7rem;
    }
  }
</style>



