<template>
  <div>
    <Header></Header>
    <audio :src="music" id="audio"></audio>
    <keep-alive>
     <router-view @spot="getSong" v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <div class="circle" :class="{out:isOpen,in:!isOpen}"><img :src="play" class="play" @click="pause()"><img src="../assets/img/array_right.png" class="array_right" @click="out()"><div class="box"><p>{{ name }}</p></div><img src="../assets/img/array_left.png" class="array_left" @click="out()"></div>
    <Footer></Footer>
  </div>
</template>

<script>
import Header from '@/components/Header'
import Footer from '@/components/Footer'
export default {
  data(){
    return{
      name:"グレイ – sajou no hana",
      music:"http://132.232.74.205/music/gray.mp3",
      isPause:true,
      isOpen:false,
      play:require("../assets/img/play1.png")
    }
  },
  components: {
    Header,
    Footer
  },
  methods:{
    out:function(){
       this.isOpen==false?this.isOpen=true:this.isOpen=false
    },
    pause:function(){
      let audio = document.getElementById("audio");
      this.isPause?this.isPause = false:this.isPause = true;
      audio.paused?audio.play():audio.pause();
      audio.paused?this.play=require("../assets/img/play1.png"):this.play=require("../assets/img/pause1.png");
    },
    getSong:function(data){
       let audio = document.getElementById("audio");
       console.log(data);
       this.music=data[0];
       this.name=data[1];
       audio.autoplay = true;
       audio.play();
       this.isPause = false;
       this.play=require("../assets/img/pause1.png");
    }
  }
}
</script>

<style lang="scss" scoped>
  @keyframes out{
    from{width:1rem;background-color:rgba(255, 255, 255, .8)}to{width:4rem;background-color:rgba(53, 53, 53, .1)};
    }
  @keyframes in{
    from{width:4rem;background-color:rgba(53, 53, 53, .1)}to{width:1rem;background-color:rgba(255, 255, 255, .8)};
    }
  @keyframes wordsLoop {
    0% {
      transform: translateX(4rem);
      -webkit-transform: translateX(4rem);
        }
    100% {
      transform: translateX(-6rem);
      -webkit-transform: translateX(-6rem);
    }
  }
  audio{
    position: absolute;
    top:1rem;
  }
  .circle{
     background-color:rgba(255, 255, 255, .8);
     width: 1rem;
     height: 1rem;
     position: fixed;
     top: 2rem;
     right: 0;
     z-index: 99;
     border-radius: 0.3rem 0 0 0.3rem;
     .box{
      width:2rem;
      overflow: hidden;
      margin-left: 1rem;
     }
  }
  .out{
    width: 4rem;
    animation:out 1s;
    background-color:rgba(53, 53, 53, .1);
    .play{
      width: 1rem;
      height: 1rem;
      position: absolute;
      right: 0;
    }
    .array_right{
      width: 1rem;
      height: 0.8rem;
      position: absolute;
      left: 0;
      top: 0.1rem;
    }
    .array_left{
      display: none;
    }
    p{
      color: rgba(53,53,53,0.7);
      height: 1rem;
      width: 2rem;
      font-size: 0.5rem;
      font-weight: bold;
      line-height: 1.1rem;
      white-space: nowrap;
      display: inline-block;
      animation: 10s wordsLoop linear infinite normal;
    }
  }
  .in{
    width: 1rem;
    animation:in 1s;
    .play{
      display: none;
    }
    .array_right{
      display: none;
    }
    .array_left{
      width: 1rem;
      height: 0.8rem;
      position: absolute;
      top:0.1rem;
    }
    p{
      display: none;
    }
  }
</style>



